import React, {useEffect, useRef} from 'react'
import QRCode from 'easyqrcodejs';
import {useSelector} from "react-redux";
import AppId from "../../../configs/appId";
import {Button, Input} from 'reactstrap'
import {FormattedMessage} from "react-intl";
import {Clipboard, Download} from "react-feather";
import {toastSuccess} from "../../../utility";
import {IMAGE} from "../../../configs/app-configs";

export const ShareWithFriends = () => {

  const {user} = useSelector(state => state.customizer.appId === AppId.TP_BANK_APP ? state.auth.guest : state.auth)
  const qrCode = useRef()

  const shareUrl = `${document.location.origin}/home?refId=${user.username}`

  useEffect(() => {
    // Options
    const options = {
      text: shareUrl,
      colorDark: '#106D5A',
      correctLevel: QRCode.CorrectLevel.H,
      logo : 'data:image/svg+xml;base64,',
      dotScale : 0.5,
    }
    // Create new QRCode Object
    new QRCode(qrCode.current, options);
  }, [])

  const onClickCopy = async () => {
    await navigator.clipboard.writeText(shareUrl);
    toastSuccess(<FormattedMessage id='setting.copySuccess'/>)
  }

  const onClickDownloadQRCode = () => {
    const canvas = document.querySelector('#qrcode canvas');
    const pngUrl = canvas.toDataURL('image/png').replace('image/png', 'image/octet-stream');
    console.log('pngUrl', pngUrl);
    let downloadLink = document.createElement('a');
    downloadLink.href = pngUrl;
    downloadLink.download = 'inon-qrcode.png';
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
  }


  return (
    <React.Fragment>
      <div className='text-center my-4'>
        <div id='qrcode' ref={qrCode}/>
        <div>
          <span className='text-primary cursor-pointer' onClick={onClickDownloadQRCode}><Download size={24} /></span>
        </div>
        <div className='d-flex align-items-center justify-content-center mt-3'>
          <Input value={shareUrl} disabled/>
          <span className='cursor-pointer ml-2 text-primary' onClick={onClickCopy}><Clipboard/></span>
        </div>
      </div>
    </React.Fragment>
  )
}
